
@charset "UTF-8";
html {
  font-size: 1vh;
}
::-webkit-scrollbar {
  width: 0px;
}
.animated.animated-in {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}
#app {
  font-family: 'microsoft yahei', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 4vw;
  width: 100vw;
  /* overflow:hidden; 这个加上在切换路由时动画有bug  */
  color: #eef2f5;
}
#app ul,
  #app li,
  #app div {
    border-color: #186591;
}
#app .z1 {
    z-index: 1;
}
#app .z2 {
    z-index: 2;
}
#app .z3 {
    z-index: 3;
}
#app .z4 {
    z-index: 4;
}
#app .z5 {
    z-index: 5;
}
#app .z6 {
    z-index: 6;
}
body {
  overflow-x: hidden;
}
.text-no-wrap {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.small {
  font-size: 3.75vw;
}
.iconfont {
  display: inline-block;
}
.wholepage {
  height: 25rem;
  width: 100vw;
}
.view {
  position: absolute;
  left: 0px;
  right: 0px;
}
.bg {
  box-sizing: border-box;
  width: 100vw;
  height: 25rem;
  background-size: cover;
}
.light_color_2 {
  color: #00d1ff;
}
.light_color {
  color: #99b6c3;
}
.primary_color {
  color: #eef2f5;
}
.btn-noborder {
  border: none;
  color: #eef2f5;
  background: transparent;
  outline: none;
}
input {
  outline: none;
}
._right {
  float: right;
}
._relative {
  position: relative;
}
._absolute {
  position: absolute;
}
._fixed {
  position: fixed;
}
.ripple {
  background-color: rgba(255, 255, 255, 0.4);
  -webkit-animation: ripple 1s forwards cubic-bezier(0, 0, 0.2, 1);
          animation: ripple 1s forwards cubic-bezier(0, 0, 0.2, 1);
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #505460;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #505460;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #505460;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #505460;
}
@-webkit-keyframes ripple {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
}
80% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    opacity: 0;
}
}
@keyframes ripple {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
}
80% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    opacity: 0;
}
}
@-webkit-keyframes slideOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
}
@keyframes slideOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
}
@-webkit-keyframes slideIn {
from {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
@keyframes slideIn {
from {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
@-webkit-keyframes thirdSildeOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
}
@keyframes thirdSildeOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
}
@-webkit-keyframes thirdSildeIn {
from {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
@keyframes thirdSildeIn {
from {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
.slideIn {
  -webkit-animation-name: slideIn;
  animation-name: slideIn;
}
.slideOut {
  -webkit-animation-name: slideOut;
  animation-name: slideOut;
}
.thirdSildeOut {
  -webkit-animation-name: thirdSildeOut;
  animation-name: thirdSildeOut;
}
.thirdSildeIn {
  -webkit-animation-name: thirdSildeIn;
  animation-name: thirdSildeIn;
}



